import React, { useState } from 'react';
import { connect } from 'react-redux';
import { addToCart } from '../../../redux/cart/action';
import ReusableButton from '../../../reusable/button';
import Input from './../../../reusable/input';


const ProductBox = ({ cartProducts, product }) => {
    const [quantity, setQuantity] = useState(0);

    const handleChange = (e) => {
        setQuantity(e.target.value)
    }
    
    return (
        <div className="product-box p-4 border bg-primary shadow text-center d-flex flex-column">
            <Input
                classes={{
                    inputGroup: 'd-flex flex-column-reverse',
                    formControl: 'w-100',
                    label: 'font-size-md text-white text-left',
                    prepend: 'd-none',
                    append: 'd-none'
                }}
                controlId="qty"
                type="number"
                name="qty"
                label="Qty"
                labelFor="qty"
                placeholder="1"
                onChange={handleChange}
            />

            <Input
                classes={{
                    inputGroup: 'd-flex flex-column-reverse mt-3 w-100',
                    formControl: 'w-100',
                    label: 'font-size-md text-white text-left',
                    prepend: 'd-none',
                    append: 'd-none'
                }}
                as="select"
                controlId="size"
                type="number"
                name="size"
                label="Size"
                labelFor="size"
            >
                <option value="14">14</option>
                <option value="15">15</option>
                <option value="16">16</option>
            </Input>

            <ReusableButton
                classes={{
                    btn: 'product-box-btn bg-orange text-uppercase mt-3',
                    icon: 'fas fa-shopping-cart mr-2'
                }}
                label="add to cart"
                text="Add To Cart"
                onClick={() => addToCart(product, cartProducts, quantity)}
            />

            <ReusableButton
                classes={{
                    btn: 'product-box-btn bg-dark text-uppercase mt-2',
                }}
                to="#"
                label="check availability"
                text="Check Availability"
            />
        </div>
    );
}

const mapStateToProps = state => ({
    cartProducts: state.cartProducts
});

export default connect(mapStateToProps)(ProductBox);